.about {
    min-height: 100vh;
    background: #fafafa;
    box-shadow: 0 0 2px #bebebe;
}

.about__header {
    padding: 10px;
    color: #ffca28;
    text-align: center;
    font-size: 28px;
    letter-spacing: 2px;
    text-shadow: 0.5px 0.5px 1px #212121;
}

.about__experience {
    color: #212121;
    display: flex;
}
.about__duties {
    list-style: none;
}

.about__experience:last-child {
    flex: 1;
}

.about__date {
    min-width: 70px;
    width: 15%;
    padding: 10px 20px;
    border-right: 2px solid #ffca28;
    position: relative;
    text-align: right;
    font-size: 14px;
}

.about__date::before {
    content: "";
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: #ffca28;
    position: absolute;
    top: 18px;
    right: -7px;
}

.about__content {
    padding: 10px 20px;
}

.about__name {
    padding-bottom: 10px;
    font-size: 20px;
    letter-spacing: 2px;
}

.about__tech {
    padding-top: 10px;
    font-size: 16px;
    letter-spacing: 2px;
}
