@font-face {
    font-family: "Roboto Mono";
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: local("Roboto Mono"), local("RobotoMono"),
        url("./assest/fonts/RobotoMono-Bold.ttf");
}

@font-face {
    font-family: "Roboto Mono";
    font-style: normal;
    font-weight: 300;
    font-display: swap;
    src: local("Roboto Mono"), local("RobotoMono"),
        url("./assest/fonts/RobotoMono-Light.ttf");
}
@font-face {
    font-family: "Roboto Mono";
    font-style: normal;
    font-weight: 500;
    font-display: swap;
    src: local("Roboto Mono"), local("RobotoMono"),
        url("./assest/fonts/RobotoMono-Medium.ttf");
}
@font-face {
    font-family: "Roboto Mono";
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local("Roboto Mono"), local("RobotoMono"),
        url("./assest/fonts/RobotoMono-Regular.ttf");
