@font-face {
    font-family: "Roboto Mono";
    font-style: normal;
    font-weight: 600;
    font-display: swap;
    src: local("Roboto Mono"), local("RobotoMono"),
        url("./assest/fonts/RobotoMono-SemiBold.ttf");
}

* {
    margin: 0;
    padding: 0;
}
html,
body,
#root,
.App {
    height: 100%;
    width: 100%;
    background: #fafafa;
}

.App {
    display: flex;
    flex-direction: column;
}

.form_radio {
    position: absolute;
    top: 50%;
    left: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100px;
}

.header,
.footer {
    flex: 0;
    background: #fafafa;
}

.main {
    flex: 1;
    background: #252424;
    background: #fafafa;
}

.wrapper {
    margin: 0 auto;
    max-width: 1300px;
    background: #fafafa;
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    font-family: "Roboto Mono", monospace;
}

.section {
    background: #fafafa;
    min-height: 100vh;
    width: 100%;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
}

/* Media */

@media (min-width: 590px) {
    .nav a {
        padding: 10px 40px;
    }
    .home__name {
        font-size: 70px;
    }
    .home__greeting,
    .logo {
        font-size: 40px;
    }
    .home__description {
        font-size: 30px;
    }
    .nav__list {
        font-size: 16px;
    }
    .about__date {
        min-width: 150px;
    }
    .gallery__item {
        width: 29%;
    }
}

@media (min-width: 768px) {
    .flex {
        flex-direction: row;
    }
    .logo {
        text-align: left;
    }
    .header {
        padding: 20px;
    }
    .github,
    .linkedin,
    .vk,
    .google {
        width: 40px;
        height: 40px;
    }
    .gallery__item {
        width: 22%;
    }
}

/* =Navigation */
nav {
    position: fixed;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    text-align: right;
    z-index: 100;
}
nav button {
    line-height: 25px;
    position: relative;
    padding-right: 20px;
    color: #353535;
    background: none;
    border: none;
    outline: none;
    font-family: "Roboto Mono", monospace;
    font-weight: 500;
    font-size: 14px;
}
nav button:hover .nav-label {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
}
nav .nav-dot {
    display: block;
    width: 10px;
    height: 10px;
    position: absolute;
    right: 0;
    top: 50%;
    background-color: rgba(10, 10, 10, 0.5);
    border-radius: 100%;
    transform: translateY(-50%);
    transition: all 0.1s linear;
}
/* .active .nav-dot {
    background-color: rgb(10, 10, 10);
} */
nav .nav-label {
    display: block;
    opacity: 0.5;
    visibility: hidden;
    transform: translateX(-10px);
    transition: all 0.2s cubic-bezier(0.17, 0.67, 0.83, 0.67);
}
.is-dark {
    color: #000000;
}
.is-dark .nav-dot {
    background-color: rgba(0, 0, 0);
}
nav ul {
    margin: 0;
    padding: 0;
}
nav li {
    width: auto;
    list-style: none;
}

.scroll-hint {
    position: absolute;
    bottom: 30px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
