.footer {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 10px;
    background: #fafafa;
    border-top: 2px solid rgba(128, 128, 128, 0.2);
}

.footer__container {
    text-align: center;
}

.github,
.linkedin,
.vk,
.google {
    width: 50px;
    height: 50px;
}

.github__circle,
.linkedin__circle,
.google__circle,
.vk__path {
    fill: #303030;
    stroke: #303030;
    stroke-width: 1;
}

.github__path,
.google__path,
.linkedin__path {
    fill: #fafafa;
    stroke: #fafafa;
    stroke-width: 1;
}

.footer__link {
    padding: 10px;
}

.footer__link:hover .github__circle,
.footer__link:hover .linkedin__circle,
.footer__link:hover .google__circle,
.footer__link:hover .vk__path {
    fill: #000000;
    stroke: #000000;
}
