.home__content {
    height: 100vh;
    overflow: visible;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.home__greeting {
    font-size: 36px;
}

.home__name {
    font-weight: 900;
    font-size: 64px;
    letter-spacing: 2px;
    color: #ffca28;
    text-transform: uppercase;
    text-shadow: 0.5px 0.5px 1px #212121;
}

.home__description {
    padding: 20px;
    font-size: 24px;
    letter-spacing: 1px;
}

.home__greeting,
.home__name,
.home__description {
    font-family: "Roboto Mono", monospace;
    font-weight: 500;
}
